import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { NgModule } from '@angular/core';

import {RouterModule, Routes} from '@angular/router';
import { HttpModule } from '@angular/http';
import { ReactiveFormsModule } from '@angular/forms';

import { MenuModule, PanelModule, ChartModule, InputTextModule, ButtonModule,
   InputMaskModule, InputTextareaModule, EditorModule, CalendarModule,
   RadioButtonModule, FieldsetModule, DropdownModule, MultiSelectModule, ListboxModule,
   SpinnerModule, SliderModule, RatingModule, DataTableModule, ContextMenuModule,
   TabViewModule, DialogModule, StepsModule, ScheduleModule, TreeModule, DataGridModule,
   TooltipModule, ConfirmationService, ConfirmDialogModule, GrowlModule, DragDropModule,
   GalleriaModule, OrganizationChartModule
} from 'primeng/primeng';

import { AppComponent } from './app.component';
import { SettingsComponent } from './settings/settings.component';
import { StatisticComponent } from './reuseables/statistic/statistic.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { ProjectsComponent } from './projects/projects.component';
import { FielderrorComponent } from './reuseables/fielderror/fielderror.component';
import { AlltimesComponent } from './alltimes/alltimes.component';
import { TimesheetComponent } from './timesheet/timesheet.component';
import { ProfileComponent } from './profile/profile.component';
import { CardComponent } from './reuseables/card/card.component';
import { ToSpacePipe } from './pipes/to-space.pipe';


const appRoutes: Routes = [
  { path: 'dashboard', component: DashboardComponent },
  { path: 'projects', component: ProjectsComponent },
  { path: 'settings', component: SettingsComponent },
  { path: 'alltimes', component: AlltimesComponent },
  { path: 'timesheet', component: TimesheetComponent },
  { path: 'profile', component: ProfileComponent },
  { path: '', redirectTo: '/dashboard', pathMatch: 'full' },
  ];

@NgModule({
  declarations: [
    AppComponent,
    SettingsComponent,
    StatisticComponent,
    DashboardComponent,
    ProjectsComponent,
    FielderrorComponent,
    AlltimesComponent,
    TimesheetComponent,
    ProfileComponent,
    CardComponent,
    ToSpacePipe
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    RouterModule.forRoot(appRoutes, { enableTracing: true }),
    ReactiveFormsModule,
    MenuModule,
    PanelModule,
    ChartModule,
    InputTextModule,
    InputMaskModule,
    InputTextareaModule,
    EditorModule,
    ButtonModule,
    RadioButtonModule,
    FieldsetModule,
    CalendarModule,
    DropdownModule,
    MultiSelectModule,
    ListboxModule,
    SpinnerModule,
    SliderModule,
    RatingModule,
    DataTableModule,
    ContextMenuModule,
    TabViewModule,
    DialogModule,
    StepsModule,
    ScheduleModule,
    TreeModule,
    DataGridModule,
    TooltipModule,
    ConfirmDialogModule,
    GrowlModule,
    DragDropModule,
    GalleriaModule,
    OrganizationChartModule,
  ],
  providers: [ ConfirmationService ],
  bootstrap: [AppComponent]
})
export class AppModule { }



